// CreatePost.js
import React, { useState } from "react";
import {
  Box,
  Divider,
  Typography,
  Button,
  TextField,
  Paper,
  Avatar,
  IconButton,
} from "@mui/material";
import { AttachFile, InsertPhoto } from "@mui/icons-material";

import { EditOutlined, DeleteOutlined } from "@mui/icons-material";

import Dropzone from "react-dropzone";
import FlexBetween from "../components/FlexBetween";
import { useEffect } from "react";
import { AuthApi } from "../context/user";
import axios from "axios";
import { AlertApi } from "../context/AlertContext";

const CreatePost = ({ setpostsData }) => {
  const [containsImage, setContainsImage] = useState(false);
  const [image, setImage] = useState(null);
  const [caption, setCaption] = useState("");
  const [loader, setLoader] = useState(true);
  const [posting, setPosting] = useState(false);

  const { user } = AuthApi();
  const { setAlert } = AlertApi();
  useEffect(() => {
    if (caption || image) {
      setLoader(false);
    } else {
      setLoader(true);
    }
  }, [image, caption]);

  const handleSubmit = async () => {
    setLoader(true);
    setPosting(true);
    const formData = new FormData();
    formData.append("content", caption);
    formData.append("image", image);

    const config = {
      headers: {
        "Content-Type": "multipart/form-data",
        authorization: `Bearer ${user?.Token}`,
      },
    };

    const url = `${process.env.REACT_APP_API_KEY}/admins/post`;
    try {
      const res = await axios.post(url, formData, config);

      setpostsData((prevItems) => [res.data.post, ...prevItems]);
      setCaption("");
      setImage(null);

      setAlert({
        type: "success",
        message: "Success Post",
      });
    } catch (error) {
      setAlert({
        type: "error",
        message: "Somthing went Wrong ",
      });
    }
    setPosting(false);

    setLoader(false);
  };
  return (
    <Paper className="p-4 mt-2 space-y-4 w-96 md:full bg-white rounded-md shadow-md">
      <div className="flex items-center space-x-2">
        <Avatar src={user?.profile} alt="User Avatar" />
        <TextField
          id="outlined-multiline-flexible"
          label="What's on your mind?"
          multiline
          maxRows={4}
          fullWidth
          value={caption}
          variant="outlined"
          onChange={(e) => {
            setCaption(e.target.value);
          }}
        />
      </div>

      {containsImage && (
        <Box border={`1px solid gray`} borderRadius="5px" mt="1rem" p="1rem">
          <Dropzone
            acceptedFiles=".jpg,.jpeg,.png"
            multiple={false}
            onDrop={(acceptedFiles) => setImage(acceptedFiles[0])}
          >
            {({ getRootProps, getInputProps }) => (
              <FlexBetween>
                <Box
                  {...getRootProps()}
                  border={`2px dashed cyan`}
                  p="1rem"
                  width="100%"
                  sx={{ "&:hover": { cursor: "pointer" } }}
                >
                  <input {...getInputProps()} />
                  {!image ? (
                    <p>Add Image Here</p>
                  ) : (
                    <FlexBetween>
                      <Typography>{image.name}</Typography>
                      <EditOutlined />
                    </FlexBetween>
                  )}
                </Box>
                {image && (
                  <IconButton
                    onClick={() => setImage(null)}
                    sx={{ width: "15%" }}
                  >
                    <DeleteOutlined />
                  </IconButton>
                )}
              </FlexBetween>
            )}
          </Dropzone>
        </Box>
      )}

      <Divider sx={{ margin: "1.25rem 0" }} />
      <div className="flex items-center justify-between">
        <div className="flex space-x-2">
          <IconButton
            color="primary"
            onClick={() => setContainsImage(!containsImage)}
          >
            <InsertPhoto />
          </IconButton>
          <IconButton color="primary">
            <AttachFile />
          </IconButton>
        </div>
        <Button
          variant="contained"
          color="primary"
          disabled={loader && user}
          onClick={handleSubmit}
        >
          {!posting ? "Post" : "Posting....."}
        </Button>
      </div>
    </Paper>
  );
};

export default CreatePost;
